@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Book.ttf);
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(fonts/Avenir-Heavy.ttf);
  font-weight: 800;
}

body {
  margin-top: 20px;
  margin-bottom: 0;
}

html {
  font-family: Avenir, sans-serif;
  font-size: 16px;
}

.labelSearchBar {
  display: block;
  position: relative;
}

.searchBar {
  font-family: inherit;
  font-size: inherit;
  color: #3d4e61;
  background-color: #fff;
  box-sizing: border-box;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  outline: none;
}

.bigBar {
  padding-left: 62px;
  height: 70px;
  margin-bottom: 20px;
  line-height: 22px;
}

.smallBar {
  padding-left: 33px;
  height: 42px;
  font-size: 14px;
  line-height: 19px;
}

.bigBar:hover,
.smallBar:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.bigBar:focus,
.smallBar:focus {
  background-color: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: bold;
}

.searchLogo {
  position: absolute;
}

.bigLogo {
  left: 26px;
  top: 25px;
}

.smallLogo {
  width: 11px;
  height: 11px;
  left: 13px;
  top: 15px;
}
